<template>
  <layout :active="3">
    <div class="protfolio">
      <div class="yearlist">
        <h3><em>作品</em>日期</h3>
        <div class="menus">
          <div v-for="year in yearsRoute" @click="gotoSlider(year)" :class="yaerActive == year? 'active' : '' ">
          {{ year }}
          </div>
        </div>
      </div>
      <slides ref="protfolio" v-on:SlidePointProtfolio="SlidePointProtfolio" :database="currentdt" :slidetitle="slidetitle" :displays="displays" :startIndex="startIndex"></slides>
      <div class="search">
        <input type="text"  placeholder="搜索" ref="search" name="" @keyup.enter="search">
        <button class="fa fa-search" @click="search"></button>
      </div>
    </div>
  </layout>
</template>

<script>
  import Layout from '../modules/layout.vue'
  import Slides from '../modules/slider';
  import {portfolio} from '../mock-data/portfolio.js'
  export default {
    components:{
      Layout,
      Slides
    },
		data(){
			return{
				currentdt:portfolio,
				yearsRoute:[],  // 年份路由
        years:[],       // 年份【用于页面显示使用】
				yaerActive:"",  // 默认选中的年份
        yearsIdx:[],    // 年份【用于定位使用】
				startIndex:1,   // 开始的图片轮播
        slidetitle:[],  // 轮播控制文本
				displays:7      // 显示几个图片
			}
		},
		methods:{
			initdata(years){
        var year = years;
        if(!year) year = this.yearsRoute[0] || '';
        if(year){
          this.yaerActive = year;
        }
			},
      search(e){
        var val = this.$refs.search.value;
        var idx = this.slidetitle.indexOf(val);
        this.$refs.protfolio.$refs.carousel.goFar(idx);
      },
      gotoSlider(year){
        this.yaerActive = year;
        var idx = this.yearsIdx.indexOf(year);
        this.$refs.protfolio.$refs.carousel.goFar(idx);
      },
      initYears(){
        this.yearsRoute = [];
        this.yearsIdx = [];
        for(let i=0;i< this.currentdt.length;i++){
          let num = this.currentdt[i]['year'];
          let title = this.currentdt[i]['desc'];
          this.slidetitle.push(title)
          this.yearsIdx.push(num);
          if(this.yearsRoute.indexOf(num) === -1){
            this.yearsRoute.push(num);
          }
        }
        this.yearsIdx.reverse()
        this.currentdt.reverse()
        this.slidetitle.reverse()
        this.yearsRoute.reverse()
        // console.log("yearsIdx::",this.yearsIdx)
      },
      SlidePointProtfolio(idx){
        this.yaerActive=this.yearsIdx[idx];
      }
		},
    created(){
      this.initYears();
    	this.initdata();
    }

  }
</script>

<style lang="less" >
.protfolio{
  .active{
    color:#fff;
  }
  .carousel-nav{
    // height: 228px;
    // overflow: auto;
  }
}
.search{
	position:absolute;
	right:50px;
	top:65px;
	input{
		border:0;
		border-bottom:2px solid #fff;
		background:none;
		line-height:30px;
		padding:0 5px;
		outline:none;
		color:#fff;
		float: left;
	}
	button{
		background:none;
		color:#fff;
		border:0;
		outline:none;
		font-size:18px;
		padding:5px;
		cursor:pointer;
		margin-top:5px;
	}
}
.yearlist{
  position: absolute;
  z-index: 9099;
  right: 70px;
  display: block;
  top :0;
  bottom :0;
  margin: auto;
	height: 281px;
  background: #000;
  /*定义滚动条宽高及背景，宽高分别对应横竖滚动条的尺寸*/
  .menus::-webkit-scrollbar{
    width: 5px;
    // height: 16px;
    background-color: #f5f5f5;
  }
  /*定义滚动条的轨道，内阴影及圆角*/
  .menus::-webkit-scrollbar-track{
    box-shadow: inset 0 0 6px rgba(0,0,0,.3);
    // border-radius: 5px;
    background-color: #000;
  }
  /*定义滑块，内阴影及圆角*/
  .menus::-webkit-scrollbar-thumb{
    /*width: 10px;*/
    height: 20px;
    border-radius: 5px;
    box-shadow: inset 0 0 6px rgba(0,0,0,.3);
    background-color: #fff;
  }

  .menus{
	  height: 228px;
	  overflow: hidden;
	  overflow-y:auto;
    cursor: pointer;
  }
	h3{
		color:#fff;
		font-size:14px;
		line-height:40px;
		em{
			border-bottom:2px solid #fff;
  			display:inline-block;
		}
	}
	.active{
		a{color:#fff;}
	}
	div{
		color:#666666;
		padding-top:15px;
		a{
			color:#676767;
		}
	}
	
} 
</style>


